//Start
document.addEventListener( 'DOMContentLoaded', function() {

	startApp();
    
} );

function startApp() {
    
	openMenucoll();
    selectOption()
    
}

//COLLECTION MENU

function openMenucoll() {
    
	const front = document.querySelector('.collection-menu__front');
	
	front.addEventListener( 'click', showMenu );
    
}

function showMenu() {

	const genesis = document.getElementById('genesis');
	const orion = document.getElementById('orion');
	const feline  = document.getElementById('feline');

	genesis.classList.toggle('display-none');
	orion.classList.toggle('display-none');
	feline.classList.toggle('display-none');
	
}

//SELECT OPTION

function selectOption() {

    const genesis = document.getElementById('genesis');
	const orion = document.getElementById('orion');
	const feline  = document.getElementById('feline');

    genesis.addEventListener( 'click', showGenesis );
    orion.addEventListener( 'click', showOrion );
    feline.addEventListener( 'click', showFeline );
    
}

function showGenesis() {

    const orion = document.getElementById('orion');
	const feline  = document.getElementById('feline');

    orion.classList.toggle('display-none');
	feline.classList.toggle('display-none');

}

function showOrion() {

    const genesis = document.getElementById('genesis');
	const feline  = document.getElementById('feline');

    genesis.classList.toggle('display-none');
	feline.classList.toggle('display-none');

}

function showFeline() {

    const genesis = document.getElementById('genesis');
	const orion  = document.getElementById('orion');

    genesis.classList.toggle('display-none');
	orion.classList.toggle('display-none');

}

